                </el-table-column>
                <el-table-column
                    prop="address"
                    label="地址">
                </el-table-column>
                <el-table-column
                    label="供应商状态">
                    <template>
                        <el-switch></el-switch>
                    </template>
                </el-table-column>
                <el-table-column                  
